	import cardItem from '@/components/card-item/card-item.vue'
	import { nameCardList, nameCardProList } from '@/common/vmeitime-http/index.js'
    export default {
		components:{
			cardItem
		},
        data() {
            return {
				loadMoreText: "加载中...",
				showLoadMore: false,
				bStop: false,
				list: [],
				pages: 0,
				projectType: '0',
				params:{
					pageSize: 15,
					pageNum: 1
				}
            }
        },
		onPullDownRefresh() {
			this.refresh()
			setTimeout(function () {
				uni.stopPullDownRefresh();
			}, 1000);
		},
		onReachBottom(){
			console.log('next');
			this.scrolltolower()
		},
		onShow(){
			let sortType = uni.getStorageSync('projectSort')
			if(sortType){
				this.params.sortType = sortType
				uni.clearStorageSync('projectSort')
				this.refresh()
			}
		},
		onLoad() {
			this.refresh()
			
		},
		onShow() {
			if (this.bStop) {
				this.refresh()
			}
			this.bStop = true
		},
        methods: {
			// 名片列表
			async nameCardList(){
				let res = null;
				if (this.projectType === '0') {
					res = await nameCardList({
						...this.params
					})
				} else {
					res = await nameCardProList({
						...this.params
					})
					// res.data.data.list.forEach((item) => {
					// 	item.photoUrl = item.photoUrl
					// })
				}
				this.showLoadMore = false
				if(res.data.flag){
					this.pages = res.data.data.pages
					this.list = this.list.concat(res.data.data.list)
				}
			},
			refresh(){
				this.list=[]
				this.params = {
					...this.params,
					pageSize:15,
					pageNum:1
				}
				this.nameCardList()
			},
			changeType(value){
				if (this.projectType === value) return;
				this.list = []
				this.projectType = value
				this.params = {
					...this.params,
					pageNum:1
				}
				this.nameCardList()
			},
			// 分页
			scrolltolower() {
				console.log(this.params.pageNum, this.pages)
				if(this.params.pageNum >= this.pages || this.showLoadMore) return
				console.log(2)
				this.showLoadMore = true;
				this.params.pageNum++
				this.nameCardList()
			},
			// 返回不同类型class
			selectType(type) {
				switch(type) {
					case '2': 
						return 'type0';
					case '3': 
						return 'type1';
					default: 
						return 'type2';
				}
			},
			// 返回不同类型stirng
			selectTypeStr(type) {
				switch(type) {
					case '2':
						return '回赠名片';
					case '3':
						return '已互换名片';
					default: 
						return '已递名片';
				}
			},
        }
    }
